import React, { Component } from "react";
import "antd/dist/antd.css";
import { Icon } from "antd";
import { withRouter } from "react-router-dom";
import { fetchArtistSearchResult } from "../../redux/store";
import { fetchAlbumsSearchResult } from "../../redux/store";
import { Button, notification } from "antd";

class SearchBar extends Component {
  state = {
    input: ""
  };

  fetchResultsandRedirect() {
    fetchArtistSearchResult(this.state.input);
    fetchAlbumsSearchResult(this.state.input);
    localStorage.setItem("searchTerm", this.state.input);
    this.props.history.replace({ pathname: `/search/${this.state.input}` });
  }

  openNotification() {
    const key = `open${Date.now()}`;
    const btn = (
      <Button
        style={{
          backgroundColor: "red",
          borderColor: "#ef1a1a",
          height: "30px"
        }}
        type="primary"
        size="small"
        onClick={() => notification.close(key)}
      >
        Continue
      </Button>
    );
    notification.open({
      message: "Invalid search term.",
      description: "Please provide a meaningful search input!",
      btn,
      key
    });
  }

  handleSearch() {
    const regex = /[!?.,#%^&*]{1,}/gi;
    if (regex.test(this.state.input) === true || this.state.input === "") {
      this.openNotification();
    } else {
      this.fetchResultsandRedirect();
    }
  }
  enterPressed = event => {
    if (event.keyCode === 13) {
      const regex = /[!?.,#%^&*]{1,}/gi;
      if (regex.test(this.state.input) === true || this.state.input === "") {
        this.openNotification();
      } else {
        this.fetchResultsandRedirect();
      }
    }
  };

  render() {
    return (
      <div className="header">
        <div className="search-bar">
          <input
            className="search-input"
            type="text"
            placeholder={"Search for music..."}
            onChange={event => this.setState({ input: event.target.value })}
            onKeyUp={event => this.enterPressed(event)}
          />
        </div>
        <div className="search-clicked-icons">
          <div className="closed" onClick={() => this.props.change(false)}>
            <Icon type="close" className="close-icon-clicked" />
          </div>
          <div className="search-clicked" onClick={() => this.handleSearch()}>
            <Icon type="search" className="search-icon-clicked" />
          </div>
        </div>
      </div>
    );
  }
}

export default withRouter(SearchBar);
