import React, { Component } from "react";
import { fetchTopArtistsInfo } from "../../redux/store";
import { fetchAlbums } from "../../redux/store";
import Loader from "react-loader-spinner";
import { connect } from "react-redux";
import ArtistHeader from "./artistheader";
import ArtistTags from "./artisttags";
import ArtistBio from "./artistbio";
import AlbumsAndSimilarArtistsSection from './Albums/albumsection';
class ArtistDetails extends Component {
  state = {
    loading: true
  };
  componentDidMount() {
    if(this.props.user.userInfo !== null) {
      const name = this.props.match.params.name;
    Promise.all([fetchTopArtistsInfo(name), fetchAlbums(name)]).then(() => {
      this.setState({
        loading: false
      });
    });
    } else {
      this.props.history.replace({pathname: '/login'})
    }
    
  }
  render() {
    const artist = this.props.match.params.name;
    if (this.state.loading === false) {
      return (
        <div className="container" style={{ marginTop: "10px", marginBottom: "20px" }}>
          <ArtistHeader
            img={this.props.artistInfo[artist].image[2]["#text"]}
            name={this.props.artistInfo[artist].name}
          />
          <ArtistTags tags={this.props.artistInfo[artist].tags.tag} />
          <ArtistBio
            bio={this.props.artistInfo[artist].bio.summary.split(" <a")[0]}
          />
          <AlbumsAndSimilarArtistsSection artistName={artist} />
          
        </div>
      );
    } else {
      return (
        <div className="loader-container">
          <Loader type="Audio" color="#black" height="250" width="250" />
        </div>
      );
    }
  }
}
const mapStateToProps = state => ({
  user: state.user || {},
  artistInfo: state.artistsInfo || {}
});

const ArtistDetailsContainer = connect(
  mapStateToProps,
  null
)(ArtistDetails);

export default ArtistDetailsContainer;
