import React from "react";
import Item from "./item";
import { connect } from "react-redux";

const ItemList = props => {
  const albums = props.albums;
  const similar = props.artistInfo[props.artistName].similar.artist;
  console.log(similar);
  return (
    <div className="display-search-results">
      <div className="alb-section">
        <div className="title">Albums</div>
        {albums.map((item, i) => (
          <Item
            type={"album"}
            name={item.name}
            playcount={item.playcount}
            key={i}
            image={item.image[2]["#text"]}
          />
        ))}
      </div>

      <div className="similar-section">
        <div className="title">Similar Artists</div>
        {similar.map((item, i) => (
          <Item
            type={"artist"}
            artist={item.artist}
            name={item.name}
            key={i}
            image={item.image[2]["#text"]}
          />
        ))}
      </div>
    </div>
  );
};
const mapStateToProps = state => ({
  albums: state.albums.topAlbums || [],
  artistInfo: state.artistsInfo || {}
});

const ListContainer = connect(
  mapStateToProps,
  null
)(ItemList);

export default ListContainer;
