import React, { Component } from "react";
import MyFooter from "./lastFmComponents/Footer/footer";
import Header from "./lastFmComponents/Header/header";
import HomePageContent from "./lastFmComponents/homepagecontent";
import Login from "./lastFmComponents/Login/login";
import SearchPage from "./lastFmComponents/Search/searchpage";
import ArtistDetails from './lastFmComponents/ArtistDetails/artist';
import "antd/dist/antd.css";
import "./App.css";
import { Route, Switch } from "react-router-dom";

class App extends Component {
  render() {
    return (
      <div className="app">
        <header>
          <Header />
        </header>
        <Switch>
          <Route exact path="/" component={HomePageContent} />
          <Route exact path="/login" component={Login} />
          <Route exact path="/search/:name" component={SearchPage} />
          <Route exact path="/artists/:name" component={ArtistDetails} />
          {/* <Route exact path="/tag/:name" component={TagDetails} /> */}
        </Switch>
        <MyFooter />
      </div>
    );
  }
}

export default App;
